#devicesButtonGroupContainer {
    position: fixed;
    bottom: $o-space-40;
    left: $o-space-40;
    z-index: 9999;
    background: $background300;
    border-radius: $o-border-radius-small;
    padding: $o-space-10;

    .devicesButtonGroup {
        display: flex;

        .components-button {
            position: relative;
            border-radius: 0;
            color: rgb(160 160 160);

            &:after {
                font-family: dashicons;
                position: absolute;
                top: -2px;
                right: 8px;
                font-size: 12px;
            }

            &.is-pressed {
                background: none;
                color: var(--wp-admin-theme-color);

                &.default:after {
                    content: '\f155';
                }
            }

            &.default {
                &:after {
                    content: '\f154';
                }
            }
            &.is-secondary {
                border-left: 1px solid $o-main-color-border;
                transition: $o-transition-fast;
                &:hover {
                    color: var(--wp-admin-theme-color);
                }
            }
        }
    }
}

.o-editor {
    .responsive-buttons-container {
        .components-button-group {
            background: $background300;
            border-radius: $o-border-radius-small;
            padding: $o-space-10;
            margin-bottom: $o-space-10;

            .components-button {
                position: relative;
                margin-bottom: 0;
                border-radius: 0;
                color: rgb(160 160 160);

                &:after {
                    font-family: dashicons;
                    position: absolute;
                    top: -2px;
                    right: 8px;
                    font-size: 12px;
                }

                &.is-pressed {
                    background: none;
                    color: var(--wp-admin-theme-color);

                    &.default:after {
                        content: '\f155';
                    }
                }

                &.default {
                    &:after {
                        content: '\f154';
                        color: var(--wp-admin-theme-color);
                    }
                }

                &.valid {
                    &:after {
                        content: '\f15e';
                        color: var(--wp-admin-theme-color);
                    }
                }
            }
        }
    }
}
